import uniCountdown from '@/components/uni-countdown/uni-countdown.vue';
import moveVerify from '@/components/helang-moveVerify/helang-moveVerify.vue';
import { getMobileVerify } from '@/api/perfectDatum.js';
import { upUserInfo } from '@/api/user.js';
import { mapState } from 'vuex';
export default {
	data() {
		return {
			username: '',
			phone: '',
			verify: '',
			//验证的规则
			rules: {
				username: {
					rule: /\S/,
					msg: '姓名不能为空'
				},
				phone: {
					rule: /^1[3|4|5|6|7|8][0-9]\d{8,8}$/,
					msg: '手机号码不正确'
				},
				verify: {
					rule: /^[A-Za-z0-9]{6}$/,
					msg: '验证码不正确'
				}
			},
			resultData: {},
			isVerify: false,
			isSend: true,
			sendText: '发送验证码'
		};
	},
	computed: {
		...mapState(['userLogo', 'userName', 'userStatus'])
	},
	components: { 'move-verify': moveVerify, uniCountdown },
	methods: {
		/* 校验结果回调函数 */
		verifyResult(res) {
			if (res.flag) {
				this.isVerify = true;
			}
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset() {
			this.$refs.verifyElement.reset();
			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		isOver() {
			this.sendText = '再次发送';
			this.isSend = true;
		},
		sendVerify() {
			getMobileVerify({ mobile: this.phone })
				.then(response => {
					if (response.data.msg == 'SUCCESS') {
						uni.showToast({ title: '发送成功,请查收', icon: 'none' });
					}
				})
				.catch(() => {
					uni.showToast({ title: '发送失败,请稍后再试', icon: 'none' });
				});
			this.isSend = false;
		},
		//
		submit() {
			if (!this.validate('username')) return;
			if (!this.validate('phone')) return;
			if (!this.validate('verify')) return;
			uni.showLoading({
				title: '提交数据中'
			});

			upUserInfo({
				name: this.username,
				mobile: this.phone,
				userCheckCode: this.verify
			})
				.then(res => {
					if (res.statusCode === 200) {
						uni.showToast({
							title: '提交成功',
							icon: 'none',
							duration: 1000,
							success: function() {
								setTimeout(function() {
									uni.navigateTo({
										url: `/pages/home/index`
									});
								}, 2000);
							}
						});
					} else {
						uni.showToast({ title: '提交数据失败，请重试', icon: 'none' });
					}
				})
				.catch(() => {
					uni.showToast({ title: '提交数据失败，请重试', icon: 'none' });
				});
		},
		//判断验证是否符合要求
		validate(key) {
			let bool = true;
			if (!this.rules[key].rule.test(this[key])) {
				//提示信息
				uni.showToast({
					title: this.rules[key].msg,
					icon: 'none'
				});
				//取反
				bool = false;
				return false;
			}
			return bool;
		}
	}
};
